import React, { FC, useState } from "react";
import './Contacts.scss'
import LocationOnIcon from '@mui/icons-material/LocationOn';
import EmailIcon from '@mui/icons-material/Email';
import LocalPhoneIcon from '@mui/icons-material/LocalPhone';
import AccessTimeIcon from '@mui/icons-material/AccessTime';
import { NavLink } from "react-router-dom";
import ArrowForwardIosIcon from '@mui/icons-material/ArrowForwardIos';
import Breadcrumb from "../../components/breadcrumb/Breadcrumb";
import { send } from 'emailjs-com';

const Contacts: FC = () => {
    const [userName, setUserName] = useState<string>("")
    const [message, setMessage] = useState<string>("")
    const [phone, setPhone] = useState<string>("+7")

    const onChangePhone = (e: string) => {
        const phoneno = /^\+?([0-9]{2})\)?[-. ]?([0-9]{4})[-. ]?([0-9]{4})$/;
        console.log(e)
        if (e.length === 1 && Number(e)) {
            setPhone("+7" + e)
        }
        else if (Number(e.substring(1)) || "") {
            setPhone(e)
        }

    }

    const onSubmit = (e: React.FormEvent<HTMLFormElement>) => {
        e.preventDefault()
        console.log("submit")
    }

    const onSend = () => {
        send(
            'service_p6ld4zl',
            'template_ffq5i2x',
            { from_name: " ", message: " ", phone: "990" },
            'kjpQu_9IRGMk0L8Rb'
        )
            .then((response) => {
                console.log('SUCCESS!', response.status, response.text);
            })
            .catch((err) => {
                console.log('FAILED...', err);
            });
    }

    return (
        <main className="contacts">
            {/* <section>    </section> */}
            <Breadcrumb title="    " />
            <div className="contacts__info">
                <div className="info-city"></div>
                <div className="info-cont">
                    <div className="info__box">
                        <div className="info__box-item">
                            <div className="info-img"><LocationOnIcon sx={{ color: "#fff" }} /></div>
                            <div className="info__box-details">
                                <span className="details-title"></span>
                                <span className="details-info">.</span>
                            </div>
                        </div>
                        <div className="info__box-item">
                            <div className="info-img info-img_mail"><EmailIcon sx={{ color: "#fff" }} /></div>
                            <div className="info__box-details">
                                <span className="details-title">E-mail</span>
                                <a className="details-link" href="mailto:test@mail.ru">test@mail.ru</a>
                                <span className="details-info"> </span>
                            </div>
                        </div>
                    </div>
                    <div className="info__box">
                        <div className="info__box-item">
                            <div className="info-img"><LocalPhoneIcon sx={{ color: "#fff" }} /></div>
                            <div className="info__box-details">
                                <span className="details-title"></span>
                                <a className="details-link" href="tel:+79879876136">+7 (987) 987-61-36</a>
                            </div>
                        </div>
                        <div className="info__box-item">
                            <div className="info-img info-img_time"><AccessTimeIcon sx={{ color: "#fff" }} /></div>
                            <div className="info__box-details">
                                <span className="details-title"> </span>
                                <span className="details-info">.  .:  9:00  18:00</span>
                                <span className="details-info">
                                            :</span>
                                <span className="details-info"> 10:00  20:00</span>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div className="contacts__information">
                <h2> </h2>
                <p>
                              
                    .        ,  ,   
                       :     
                     (Email).
                </p>
                <p>  : WhatsApp</p>
                <NavLink className="link-company" to="/stroyka/company" >    <ArrowForwardIosIcon fontSize="small" sx={{ color: "#ff8506" }} /></NavLink>
            </div>
            <hr></hr>
            <div className="form">
                <h3> </h3>
                <form onSubmit={e => onSubmit(e)}>
                    <div className="form-input">
                        <div className="form__box">
                            <div className="form__box-item">
                                <span className="form-label"> : <span className="required">*</span></span>
                                <input type="text" onChange={e => setUserName(e.target.value)} />
                            </div>
                            <div className="form__box-item">
                                <span className="form-label">:</span>
                                <textarea onChange={e => setMessage(e.target.value)}></textarea>
                                <span className="form-label_dop">  </span>
                            </div>

                        </div>
                        <div className="form__box">
                            <div className="form__box-item">
                                <span className="form-label">: <span className="required">*</span></span>
                                <input type="text" placeholder="+7" maxLength={12} onChange={e => onChangePhone(e.target.value)} value={phone} />
                            </div>
                        </div>
                    </div>

                    <div className="form-footer">
                        <div className="pull-left"><label className="required">*</label><label> -  </label></div>
                        <button className="pull-right" type="submit"></button>
                    </div>
                </form>

            </div>

        </main>
    )
}

export default Contacts